@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@700&family=Poppins:wght@400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

*{
    margin: 0;
    padding: 0;
    /* box-sizing: border-box; */
    font-family: "Poppins", sans-serif;
  }
.center{
  position: relative;
  /* top: 10%; */
  left: 50%;
  -webkit-transform: translate(-55%, 0%);
          transform: translate(-55%, 0%);
  width: 400px;
  background: rgba(255, 252, 252, 0.945);
  border-radius: 10px;
  box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.349);
}
.center h1{
  text-align: center;
  padding: 20px 0;
  border-bottom: 1px solid rgb(43, 38, 38);
}
.center .main_div{
  padding: 0 45px;
  box-sizing: border-box;
}
.main_div .form-group{
  position: relative;
  /* border-bottom: 2px solid #080808; */
  margin: 30px 0;
}
.form-group input{
  width: 100%;
  padding: 0 5px;
  height: 40px;
  font-size: 16px;
  border: none;
  background: none;
  outline: none;
  color: #080808;
}
.form-group label{
  position: absolute;
  top: 60%;
  left: 5px;
  color: #0f0202;
  -webkit-transform: translateY(-75%);
          transform: translateY(-75%);
  font-size: 16px;
  pointer-events: none;
  transition: .5s;
}
.form-group span::before{
  content: '';
  position: absolute;
  top: 40px;
  left: 0;
  width: 0%;
  height: 2px;
  background: #11171b;
  transition: .5s;
}
.form-group input:focus ~ label,
.form-group input:valid ~ label{
  top: -1px;
  color: #0d1920;
}
.form-group input:focus ~ span::before,
.form-group input:valid ~ span::before{
  width: 100%;
}


.button{
  width: 100%;
  height: 50px;
  border: none;
  background: rgb(124, 171, 199);
  border-radius: 25px;
  font-size: 18px;
  color: #0a0c0e;
  font-weight: 700;
  cursor: pointer;
  outline: none;
}
.button:hover{
  background-color: #b0d0e2;
  /* transition: .5s; */
}
.link{
  margin: 15px 0;
  text-align: center;
  font-size: 16px;
  color: #0f0202;
}
.link a{
  color: #0f0202;
  text-decoration: none;
}
.link a:hover{
  text-decoration: underline;
}

.navbar {
    background-color: #060b26;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  .menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
  }
  
  .nav-menu {
    background-color: #060b26;
    width: 350px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
  }
  
  .nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 10px;
    border-radius: 14px;
  }
  
  /* .nav-text a:hover {
    background-color: #1a83ff;
  } */
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  span {
    margin-left: 5px;
  }
  .style{
      text-decoration: none;
  }
